import React from 'react';
import MemProgress from './base-components/progress'
import Typography from '@material-ui/core/Typography';

export default function RAM({ memUsed, memAvail, load }) {
    return (<div className="statsContainer">
        <Typography variant="h6">RAM</Typography>
        <div style={{ width: '80%' }}>
            <MemProgress variant="determinate" value={load} caption={`${Math.round(memUsed * 100)}MB/${Math.round((memUsed + memAvail) * 100)}MB`} />
        </div>
    </div>)
}