import React from 'react';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import MemProgress from './base-components/progress'
import Temp from './base-components/temp'
import Usage from './base-components/usage'

export default function GPU({ name, temp, load, coreClock, memClock, memUsed, memTotal }) {
    return (<div className="statsContainer">
        <div><Typography variant="h6">GPU</Typography></div>
        <div><Typography variant="h6">{name}</Typography></div>
        <div><span className="sub">Core: {coreClock.toFixed(0)} MHz</span></div>
        <div><span className="sub">Mem: {memClock.toFixed(0)} MHz</span></div>
        <div style={{width: '80%'}}>
            <span className="sub">VRAM</span>
            <MemProgress variant="determinate" value={memUsed / memTotal * 100} caption={`${Math.round(memUsed)}MB/${Math.round(memTotal)}MB`}/>
        </div>

        <Box marginTop="auto">
            <div className="chartContainer">
                <Temp
                    variant="determinate"
                    value={temp}
                    size={125}
                />
            </div>

            <div className="chartContainer">
                <Usage
                    variant="determinate"
                    value={load}
                    size={125}
                />
            </div>
        </Box>

    </div>)
}