import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import {grey, green, yellow, red } from '@material-ui/core/colors';
function getColor(val) {
    if (val < 70) return green[300]
    else if (val >= 70 && val < 90) return yellow[300]
    else if (val >= 90) return red[300]
}

export default function TeUsagemp(props) {
    return (
        <Box position="relative" display="inline-flex">
            <div style={{ transform: 'rotate(-180deg)' }}>
                <CircularProgress variant="determinate" {...props} value={100} style={{ color: grey[700], position: 'absolute' }} />
                <CircularProgress variant="determinate" {...props} style={{ color: getColor(props.value) }} />
            </div>
            <Box
                top={0}
                left={0}
                bottom={0}
                right={0}
                position="absolute"
                display="flex"
                alignItems="center"
                justifyContent="center"
                flexDirection="column"
            >
                <Typography variant="subtitle2" component="div" color="textPrimary">Load</Typography>
                <Typography variant="body1" component="div" style={{color: getColor(props.value)}}>{Math.round(props.value)}%</Typography>
            </Box>
        </Box>
    );
}