import React from 'react';

import { Box, Typography } from '@material-ui/core';

import Progress from './base-components/progress'

export default function HDD({id, load, temp}) {

    return (<div className="statsContainer">
                <Typography variant="body2">HDD {id}</Typography>
                {temp ? <div><Typography variant="caption">{temp}°C</Typography></div>: null}
                {load ? <div className="chartContainer"><Progress variant="determinate" value={load} caption={`${load.toFixed(2)}%`} /></div> : null}
    </div>)
}