import React from 'react';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Temp from './base-components/temp'
import Usage from './base-components/usage'
import CPUGraphic from './cpu-components/cpu-graphic'

export default function CPU({ name, temp, load, clock, power, cores }) {
    return (<div className="statsContainer">
        <Typography variant="h6" component="div">CPU</Typography>
        <Typography variant="h6" component="div">{name}</Typography>
        <div><span className="sub">{clock.toFixed(0)} MHz</span></div>
        <div><span className="sub">{power.toFixed(0)} Watts</span></div>
        {/* <div>
            <div className="chartContainer"><CPUGraphic cores={cores} /></div>
        </div> */}

        <Box marginTop="auto">
            <div className="chartContainer">
                <Temp
                    variant="determinate"
                    value={temp}
                    size={125}
                />
            </div>
            <div className="chartContainer">
                <Usage
                    variant="determinate"
                    value={load}
                    size={125}
                />
            </div>
        </Box>

    </div>)
}